import { createRouter, createWebHistory } from 'vue-router'
import HomePage from '../views/HomePage.vue'
import WorkoutPage from '../views/WorkoutPage.vue'
import MakeTraining from '../views/MakeTraining.vue'
import ChooseTraining from '@/views/ChooseTraining.vue'
import EditTraining from '@/views/EditTraining.vue'
import DoingExercises from '@/views/DoingExercises.vue'
import RestExercises from '@/views/RestExercises.vue'
import EndExercises from '@/views/EndExercises.vue'
import LoginPage from '@/views/LoginPage.vue'
import RegisterPage from '@/views/RegisterPage.vue'
import AuthService from '@/services/AuthService'  // ‚Üê @ –¥–ª—è src/

const routes = [
    { path: '/login', name: 'Login', component: LoginPage },
    { path: '/register', name: 'Register', component: RegisterPage },

    // === –ó–ê–©–ò–©–Å–ù–ù–´–ï –†–û–£–¢–´ ===
    {
        path: '/',
        name: 'Home',
        component: HomePage,
        meta: { requiresAuth: true }
    },
    {
        path: '/workout',
        name: 'Workout',
        component: WorkoutPage,
        meta: { requiresAuth: true }
    },
    {
        path: '/make_training',
        name: 'MakeTraining',
        component: MakeTraining,
        meta: { requiresAuth: true }
    },
    {
        path: '/choose_training',
        name: 'ChooseTraining',
        component: ChooseTraining,
        meta: { requiresAuth: true }
    },
    {
        path: '/edit_training',
        name: 'EditTraining',
        component: EditTraining,
        meta: { requiresAuth: true }
    },
    {
        path: '/doing_exercises',
        name: 'DoingExercises',
        component: DoingExercises,
        meta: { requiresAuth: true }
    },
    {
        path: '/rest',
        name: 'RestExercises',
        component: RestExercises,
        meta: { requiresAuth: true }
    },
    {
        path: '/end',
        name: 'EndExercises',
        component: EndExercises,
        meta: { requiresAuth: true }
    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),  // ‚Üê –ò–°–ü–†–ê–í–õ–ï–ù–û: BASE_URL
    routes
})

// === –ì–õ–û–ë–ê–õ–¨–ù–ê–Ø –ü–†–û–í–ï–†–ö–ê –ê–í–¢–û–†–ò–ó–ê–¶–ò–ò ===
router.beforeEach(async (to, from, next) => {
    if (to.meta.requiresAuth) {
        try {
        const user = await AuthService.getCurrentUser()
        if (user) {
            next()  // ‚Üê —Ä–∞–∑—Ä–µ—à–∏—Ç—å –ø–µ—Ä–µ—Ö–æ–¥
        } else {
            next({ name: 'Login', query: { redirect: to.fullPath } })  // ‚Üê —Å —Ä–µ–¥–∏—Ä–µ–∫—Ç–æ–º
        }
        } catch (error) {
        next({ name: 'Login' })
        }
    } else {
        next()  // ‚Üê /login, /register ‚Äî –≤—Å–µ–≥–¥–∞ –¥–æ—Å—Ç—É–ø–Ω—ã
    }
})

export default router